<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <h1 class="text-h4 mb-4">Smart Home Appliances</h1>
        
        <!-- Add New Appliance Button -->
        <v-btn color="primary" class="mb-4" @click="showAddDialog = true">
          <v-icon left>mdi-plus</v-icon>
          Add New Appliance
        </v-btn>

        <!-- Appliance List -->
        <v-row>
          <v-col v-for="appliance in appliances" :key="appliance.id" cols="12" md="6" lg="4">
            <v-card>
              <v-card-title>
                {{ appliance.name }}
                <v-spacer></v-spacer>
                <v-switch
                  v-model="appliance.isOn"
                  :color="appliance.isOn ? 'success' : 'grey'"
                  @change="toggleAppliance(appliance)"
                ></v-switch>
              </v-card-title>
              
              <v-card-text>
                <div class="text-subtitle-1">Status: {{ appliance.isOn ? 'On' : 'Off' }}</div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <!-- Add Appliance Dialog -->
    <v-dialog v-model="showAddDialog" max-width="500px">
      <v-card>
        <v-card-title>Add New Light</v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="valid">
            <v-text-field
              v-model="newAppliance.name"
              label="Name"
              required
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" text @click="showAddDialog = false">Cancel</v-btn>
          <v-btn color="primary" :disabled="!valid" @click="addAppliance">Add</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ApplianceList',
  data() {
    return {
      appliances: [],
      showAddDialog: false,
      valid: false,
      newAppliance: {
        name: '',
        isOn: false
      }
    };
  },
  methods: {
    async fetchAppliances() {
      try {
        const response = await axios.get('/api/v1/appliances');
        this.appliances = response.data;
      } catch (error) {
        console.error('Error fetching appliances:', error);
      }
    },
    async toggleAppliance(appliance) {
      try {
        const updatedAppliance = { ...appliance, isOn: !appliance.isOn };
        await axios.put(`/api/v1/appliances/${appliance.id}`, updatedAppliance);
        await this.fetchAppliances();
      } catch (error) {
        console.error('Error toggling appliance:', error);
        appliance.isOn = !appliance.isOn; // Revert the switch if the request fails
      }
    },
    async updateAppliance(appliance) {
      try {
        await axios.put(`/api/v1/appliances/${appliance.id}`, appliance);
        await this.fetchAppliances();
      } catch (error) {
        console.error('Error updating appliance:', error);
      }
    },
    async addAppliance() {
      try {
        const response = await axios.post('/api/v1/appliances', {
          name: this.newAppliance.name,
          isOn: false
        });
        
        if (response.status === 200) {
          this.showAddDialog = false;
          this.newAppliance = { name: '', isOn: false };
          await this.fetchAppliances();
          alert('Light added successfully!');
        }
      } catch (error) {
        console.error('Error adding appliance:', error);
        if (error.code === 'ERR_NETWORK') {
          alert('Cannot connect to the server. Please make sure the backend is running.');
        } else {
          alert('Failed to add light. Please try again.');
        }
      }
    }
  },
  mounted() {
    this.fetchAppliances();
  }
};
</script>

<style scoped>
.v-card {
  margin-bottom: 16px;
}
</style> 